/* Section Usability */

.section-usability {
	padding-top: 127px;
	background: #181f29;
	height: 510px;
	overflow: hidden;
}

.section-usability__wrap {
	position: relative;
	display: grid;
	gap: 10px;
	grid-template-columns: 1fr 1fr;
	grid-template-areas: "section-usability__content section-usability__img";
}

.section-usability__content {
	grid-area: section-usability__content;
	padding-bottom: 150px;
	max-width: 558px;
}

.section-usability__title {
	margin-bottom: 10px;
}

.section-usability__button {
	padding: 12px 67px;
	background: #212a38;
	border-radius: 10px;

	font-weight: 400;
	font-size: 20px;
	line-height: 151.3%;
	color: #6a7c95;
	outline: 0;
	border: #212a38;
	transition: 0.3s linear;

	&:hover {
		background: rgba(106, 124, 149, 0.2);
	}

	&:focus {
		box-shadow: 0px 1px 26px #6a7395;
		color: #5ca35b;
	}
}

.section-usability__img {
	position: relative;
	grid-area: section-usability__img;
	display: flex;
	gap: 25px;
}

.section-usability__img-item {
	margin-top: 130px;
	flex-shrink: 0;
	height: 318px;
}

.section-usability__img-catalog {
	flex-shrink: 0;
}
