/* Reset and base styles  */
*,
*::before,
*::after {
	box-sizing: border-box;
}

html {
	scroll-behavior: smooth;
}

body {
	margin: 0;
	font-family: "SF Pro Display", sans-serif;
	color: #6a7c95;
	background-color: #11161e;
}

input,
textarea,
button {
	font-family: inherit;
	padding: 0;
}

label {
	cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
}

img {
	display: block;
	max-width: 100%;
	height: auto;
}

ul {
	margin: 0;
	padding: 0;
}

fieldset {
	border: none;
	margin: 0;
	padding: 0;
}

legend {
	display: block;
	margin: 0;
	padding: 0;
	border: none;
}

/* sticky footer */
html {
	height: 100%;
}

body {
	height: 100%;
	display: flex;
	flex-direction: column;
}

body > * {
	flex-shrink: 0;
}

/* Grid styles */

.container {
	max-width: 1500px;
	min-width: 1280px;
	margin: 0 auto;
	padding-left: 30px;
	padding-right: 30px;
}

/* Utility */

.none {
	display: none !important;
}

/* General */

.section__title {
	font-weight: 700;
	font-size: 42px;
	line-height: 50px;
	color: #ffffff;
}

.section__content {
	font-family: Mont;
	font-weight: 500;
	color: #717e92;
	line-height: 1.6;
	font-size: 17px;

	& + .section__content {
		margin-top: 5px;
	}

	&:last-of-type {
		margin-bottom: 55px;
	}
}
