// lg - ≥1024px
@media (max-width: 1024px) {
	.container {
		max-width: 964px;
		min-width: 768px;
		padding-left: 20px;
		padding-right: 20px;
	}

	//Header

	.nav__list {
		gap: 45px;
	}

	.nav__link {
		font-size: 16px;
	}

	.header__info {
		align-items: center;
	}

	.header__img {
		width: auto;
	}

	.header__title {
		font-size: 48px;
	}

	.header__text {
		font-size: 20px;
	}

	//About

	.card {
		width: 230px;
		padding: 15px;
	}

	.card__title {
		font-size: 24px;
	}

	//Plan

	.section-plan__row {
		flex-direction: column;
		gap: 20px;
	}

	.plan {
		width: auto;
	}

	.plan__list {
		display: grid;
		grid-template-columns: 1fr 1fr;
		gap: 10px 30px;
	}
}
