/* Section Update */

.section-update {
	padding-top: 130px;
	padding-bottom: 113px;
	background: #181f29;
}

.section-update__title {
	margin-bottom: 10px;
}

.section-update__content {
	width: 565px;
}

.section-update__row {
	width: 370px;
	display: flex;
	column-gap: 38px;
}

.section-update__social {
	display: block;
	width: 63px;
	height: 63px;
	border-radius: 50%;
	background-color: #2f89ce;

	display: flex;
	justify-content: center;
	align-items: center;
	fill: #fff;

	transition: 0.3s linear;
	outline: 0;

	&--vkontakte {
		background-color: #7294c7;
	}

	&--instagram {
		background: linear-gradient(
			43.94deg,
			#eea054 16.16%,
			#d64763 50.11%,
			#b62a99 83.68%
		);
	}

	&--facebook {
		background-color: #4e6297;
	}

	&:hover {
		opacity: 0.8;
	}

	&:focus {
		box-shadow: 0px 1px 26px #6a7395;
	}
}
