// Mobile XS - ≥440px
@media (max-width: 440px) {
	//About

	.section-about {
		padding-top: 50px;
	}

	.card {
		grid-template-areas:
			"card__img"
			"card__title"
			"card__content";
		column-gap: 40px;
	}

	.card__img {
		margin: 0 auto;
	}
	.card__title {
		justify-self: inherit;
	}
	.card__content {
		text-align: center;
	}

	//Usability

	.section-usability__img-item {
		z-index: 2;
		transform: scale(0.8);
	}
	.section-usability__img-catalog {
		z-index: 1;
		margin-left: -140px;
	}

	//Update
	.section-update__row {
		width: auto;
		gap: 7px;
		flex-wrap: wrap;
	}

	//Footer
	.footer__row {
		flex-direction: column;
		gap: 20px;
	}

	.footer__wrap {
		order: -1;
	}

	.footer__link {
		br {
			display: none;
		}
	}
}
