/* Footer */

.footer {
	padding-top: 42px;
	padding-bottom: 74px;
}

.footer__row {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.footer__link {
	font-weight: 400;
	font-size: 12px;
	line-height: 128.8%;
	color: #2d3746;
	text-decoration: none;
	transition: 0.3s linear;
	outline: 0;

	&:hover {
		color: #5ca35b;
	}

	&:focus {
		text-shadow: 0px 1px 26px rgb(92, 163, 91, 0.5);
		color: #5ca35b;
	}
}

.footer__wrap {
	display: flex;
	align-items: center;

	& > * + * {
		margin-left: 18px;
	}

	img {
		height: 34px;
	}
}

.footer__copyright {
	font-weight: 400;
	font-size: 12px;
	line-height: 128.8%;
	color: #2d3746;
}
