/* Navigation Icon */

// Ширина и высота линии в иконке
$lineWidth: 35px;
$lineHeight: 5px;
$color: #fff;

// Размеры контейнера
$width: $lineWidth;
$height: $lineHeight * 5;

.nav-icon {
	width: 50px;
	height: 50px;
	padding: 5px;
	border-radius: 50%;
	border: 1px solid transparent;
	
	display: none;
	justify-content: center;
	align-items: center;
	background-color: transparent;
	
	outline: none;
	cursor: pointer;
	transition: 0.5s linear;
}

.nav-icon__middle {
	position: relative;
	width: $width;
	height: $lineHeight;
	background-color: $color;
	
	&::before, &::after {
		content: "";
		position: absolute;
		left: 0;
		width: $lineWidth;
		height: $lineHeight;
		transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
		background-color: $color;
	}
	
	&::before {
		top: -($lineHeight*2);
	}
	
	&::after {
		top: $lineHeight*2;
	}
}

/* Active state */
.nav-icon.nav-icon--active {
	position: fixed;
	right: 20px;
}

.nav-icon.nav-icon--active .nav-icon__middle {
		background-color: transparent;
		$color: #fff;
	
	&::before, &::after {
		top: 0;
		transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
		background-color: $color;
	}
	
	&::before {
		transform: rotate(45deg);
	}
	
	&::after {
		transform: rotate(-45deg);
	}
}