// md - ≥768px
@media (max-width: 788px) {
	.container {
		max-width: 768px;
		min-width: 640px;
		padding-left: 20px;
		padding-right: 20px;
	}

	.header {
		padding-top: 20px;
	}

	.header__top .nav__list {
		display: none;
	}

	.header__top {
		margin-bottom: 100px;
	}

	.nav-icon {
		display: flex;
		z-index: 10;

		&:hover,
		&:focus {
			border: 1px solid #5ca35b;
			box-shadow: 0px 1px 26px #5ca35b;
		}
	}

	.mobile-menu {
		z-index: 9;
		position: fixed;
		top: 0;
		left: 0;
		width: 0;
		transform: translateX(0);

		height: 100%;
		background-color: #11161e;
		opacity: 0;

		transition: 0.4s ease-in;
	}

	.mobile-menu--active {
		width: 50%;
		opacity: 1;
		transform: translateX(100%);

		display: flex;
		flex-direction: column;
		gap: 30px;
		justify-content: center;
		align-items: center;

		transition: 0.4s ease-in;
	}

	.mobile-menu__link {
		font-size: 20px;
	}

	//About

	.section-about {
		padding-top: 70px;
		padding-bottom: 70px;
	}

	.section-about__content {
		width: auto;
	}

	.section-about__row {
		display: grid;
		gap: 30px;
	}

	.card {
		width: auto;
		display: grid;
		grid-template-areas:
			"card__img card__title"
			"card__img card__content";
		column-gap: 40px;
	}

	.card__img {
		grid-area: card__img;
		margin-bottom: 0;
	}

	.card__title {
		grid-area: card__title;
		margin-bottom: 0;
		justify-self: flex-start;
	}

	.card__content {
		text-align: left;
		grid-area: card__content;
	}

	//Possibility

	.section-possibility {
		padding-top: 70px;
		padding-bottom: 70px;
	}

	//Usability

	.section-usability {
		padding-top: 70px;
		height: auto;
		overflow: hidden;
	}

	.section-usability__content {
		padding-bottom: 0;
	}

	.section-usability__wrap {
		grid-template-columns: auto;
		grid-template-rows: auto 500px;
		gap: 30px;
		grid-template-areas:
			"section-usability__content"
			"section-usability__img";
	}

	.section-usability__img {
		justify-content: space-between;
		gap: 0;
	}

	.section-usability__img-item {
		margin-top: 0;
		align-self: center;
	}

	.section-usability__img-catalog {
		align-self: flex-start;
	}

	//Plan

	.section-plan {
		padding-top: 70px;
		padding-bottom: 70px;
	}

	//Update
	.section-update {
		padding-top: 70px;
		padding-bottom: 70px;
	}
}
