/* Header */

.header {
	padding-top: 105px;
	padding-bottom: 164px;
}

.header__top {
	margin-bottom: 178px;
}

.nav {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.nav__list {
	display: grid;
	grid-template-columns: auto auto auto;
	grid-template-rows: 1fr;
	gap: 75px;
}

.nav__link {
	font-family: SF Pro Display;
	font-weight: 500;
	font-size: 18px;
	line-height: 151.3%;
	color: rgba(118, 134, 159, 0.8);
	text-decoration: none;
	outline: 0;

	transition: 0.3s ease-in;

	&:hover {
		color: #5ca35b;
	}

	&:focus {
		text-shadow: 0px 1px 26px rgb(92, 163, 91, 0.5);
		color: #5ca35b;
	}
}

.nav-icon {
	display: none;
}

.mobile-menu {
	display: none;
}

.header__info {
	margin-bottom: 160px;
	display: grid;
	grid-template-columns: 1.2fr 0.8fr;
	gap: 10px;
	justify-content: space-between;
}

.header__content {
	width: 578px;
	flex-shrink: 0;
}

.header__title {
	margin-bottom: 17px;

	font-weight: 700;
	font-size: 62px;
	line-height: 101.3%;
	color: #ffffff;
}

.header__text {
	margin-bottom: 65px;

	font-weight: 400;
	font-size: 26px;
	line-height: 129.8%;
	color: #6a7c95;
}

.header__button {
	padding: 10px 35px;
	background: #5ca35b;
	border-radius: 10px;

	font-weight: 400;
	font-size: 24px;
	line-height: 151.3%;
	color: #ffffff;
	text-decoration: none;
	transition: 0.3s linear;
	outline: 0;

	&:hover,
	&:focus {
		box-shadow: 0px 1px 26px #5ca35b;
	}
}

.header__img {
	justify-self: center;
}

.header__contacts {
	position: relative;
	display: flex;
	margin-left: 54px;

	font-weight: 500;
	font-size: 18px;
	line-height: 151.3%;
	color: rgba(118, 134, 159, 0.8);
	outline: 0;

	& > * + * {
		margin-left: 90px;
	}
}

.header__phone {
	position: relative;
	color: rgba(118, 134, 159, 0.8);
	text-decoration: none;
	transition: 0.3s linear;
	outline: 0;

	&:hover {
		color: #5ca35b;
	}

	&:focus {
		text-shadow: 0px 1px 26px rgb(92, 163, 91, 0.5);
		color: #5ca35b;
	}

	&::after {
		content: "";
		display: block;
		width: 42px;
		height: 34px;

		position: absolute;
		top: 0;
		left: -54px;

		background-image: url("./../img/header/phone.svg");
		background-repeat: no-repeat;
	}
}

.header__mail {
	position: relative;
	color: rgba(118, 134, 159, 0.8);
	text-decoration: none;
	transition: 0.3s linear;
	outline: 0;

	&:hover {
		color: #5ca35b;
	}

	&:focus {
		text-shadow: 0px 1px 26px rgb(92, 163, 91, 0.5);
		color: #5ca35b;
	}

	&::after {
		content: "";
		display: block;
		width: 42px;
		height: 34px;

		position: absolute;
		top: 0;
		left: -54px;

		background-image: url("./../img/header/mail.svg");
		background-repeat: no-repeat;
	}
}
