/* Section Plan */

.section-plan {
	padding-top: 129px;
	padding-bottom: 169px;
}

.section-plan__title {
	margin-bottom: 10px;
}

.section-plan__content {
	width: 520px;
}

.section-plan__row {
	display: flex;
	justify-content: space-between;
}

.plan {
	width: 317px;
	padding-top: 46px;
	padding-bottom: 50px;
	padding-left: 25px;
	padding-right: 16px;

	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;

	background: rgb(39, 49, 64);
	box-shadow: 0px 1px 26px rgba(24, 31, 41, 0.15);
	border-radius: 15px;

	&--alt {
		background: rgb(39, 49, 64, 0.17);
	}
}

.plan__title {
	margin-bottom: -5px;

	font-family: Mont;
	font-weight: 700;
	font-size: 28px;
	line-height: 36px;
	color: #ffffff;
}

.plan__text {
	margin-bottom: 18px;

	font-family: Mont;
	font-weight: 500;
	font-size: 12px;
	color: #717e92;
}

.plan__list {
	margin-bottom: 35px;
}

.plan__item {
	font-family: Mont;
	font-weight: 500;
	font-size: 16px;
	line-height: 160.3%;
	color: #717e92;
}

.plan__item--dark {
	color: rgba(113, 126, 146, 0.4);
}

.plan__button {
	padding: 8px 78px;
	background: #6a7395;
	border-radius: 5px;
	margin-top: auto;

	font-weight: 600;
	font-size: 17px;
	color: #ffffff;
	text-decoration: none;

	transition: 0.3s linear;
	outline: 0;

	&--comand {
		background: #6a9590;
	}

	&--delit {
		background: #95936a;
	}

	&:hover {
		opacity: 0.8;
	}

	&:focus {
		box-shadow: 0px 1px 26px #6a7395;
	}
}
