// sm - ≥640px
@media (max-width: 640px) {
	.container {
		max-width: none;
		min-width: auto;
		padding-left: 20px;
		padding-right: 20px;
	}

	.header__top {
		margin-bottom: 70px;
	}

	.header__info {
		grid-template-columns: auto;
		grid-template-rows: auto auto;
		gap: 70px;
		margin-bottom: 70px;
	}

	.header__img {
		order: 1;
		width: 70%;
	}

	.header__content {
		order: 2;
		width: 100%;
	}

	.header__title {
		font-size: 32px;
	}

	.header__button {
		font-size: 20px;
	}

	.header__contacts {
		flex-direction: column;
		gap: 30px;
		& > * + * {
			margin-left: 0;
		}
	}

	//Usability

	.section-usability__button {
		font-size: 18px;
	}

	.section-usability {
		padding-top: 50px;
	}

	//Plan

	.section-plan__content {
		width: auto;
	}

	.plan__list {
		grid-template-columns: 1fr;
		gap: 10px;
	}

	.plan__button {
		padding: 8px 40px;
	}

	//Update

	.section-update__content {
		width: auto;
	}
}
