// xl - ≥1300px
@media (max-width: 1300px) {
	.container {
		max-width: 1220px;
		min-width: 1024px;
		padding-left: 30px;
		padding-right: 30px;
	}

	.header {
		padding-top: 50px;
		padding-bottom: 50px;
	}

	.header__info {
		grid-template-columns: 1fr 1fr;
	}

	.header__content {
		width: auto;
	}

	//Usability

	.section-usability__content {
		max-width: none;
	}

	.section-usability__img {
		gap: 10px;
	}
}
