/* Section About */

.section-about {
	padding-top: 150px;
	padding-bottom: 163px;
	background: #181f29;
}

.section-about__title {
	margin-bottom: 12px;
}

.section-about__content {
	width: 755px;
	line-height: 1.6;
	font-size: 17px;
}

.section-about__row {
	display: flex;
	justify-content: space-between;
}

.card {
	display: flex;
	flex-direction: column;
	align-items: center;

	width: 317px;
	padding-top: 35px;
	padding-bottom: 55px;
	padding-left: 30px;
	padding-right: 30px;

	background: #273140;
	border-radius: 15px;
	text-align: center;
}

.card__img {
	width: 100px;
	height: 100px;
	margin-bottom: 15px;
}

.card__title {
	margin-bottom: 25px;

	font-family: Mont;
	font-weight: 700;
	font-size: 28px;
	line-height: 36px;
	color: #ffffff;

	text-overflow: ellipsis;
	overflow: hidden;
}

.card__content {
	font-family: Mont;
	font-weight: 500;
	font-size: 18px;
	line-height: 142.8%;
	color: #717e92;

	& + .card__content {
		margin-top: 5px;
	}
}
